@import url(https://fonts.googleapis.com/css?family=Crimson+Text|Work+Sans:300,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.missing-page {
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.missing-page img {
    width: 40%;
    border-radius: 10px;
}


nav a {
    font-size: 20px;
    text-decoration: none;
}

.logo {
    font-size: 30px;
    margin: 10px 0;
}

.hidden {
    display: none !important;
}

.user main {
    height: 100vh;
    width: calc(100% - 200px);
    float: right;
    overflow: scroll;
}
.home-page {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    overflow: auto !important;
    height: 100vh;
}

.journal-feed {
    width: 100%;
}

.journal-feed .card-list {
    width: 100%;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    position: relative;
}

.journal-feed .card-list .entry-card {
    width: 400px;
    height: 400px;
    -webkit-transition: .3s;
    transition: .3s;
    text-align: left;
}

.journal-feed .card-list .entry-card:hover {
    box-shadow: 0px 3px 10px rgba(0,0,0,0.5);
}

.journal-feed .card-list .entry-card:active {
    -webkit-transform: translateY(3px);
            transform: translateY(3px);
}
.sweet-loading {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.entry-card {
    background: #DDF8E8;
    padding: 15px;
    width: 280px;
    margin: 10px auto;
    height: 280px;
    border-radius: 3px;
    box-sizing: border-box;
    box-shadow: 0px 3px 10px rgba(0,0,0,0.2);
    position: relative;
}

.entry-card-header {
    display: flex;
    align-items: flex-end;
}

.entry-card-header h3 {
    font-size: 1.5em;
    font-weight: 300;
    margin: 5px;
}

.entry-card-body {
    padding: 15px;
}

.highlight-baby {
    background: greenyellow;
    color: blueviolet;
}

.entry-bookmark {
    position: absolute;
    top: -3px;
    right: 20px;
    font-size: 30px;
}

.entry-bookmark svg {
    color: black;
    -webkit-transition: .8s ease-out;
    transition: .8s ease-out;
}

.entry-bookmark svg:hover {
    color: white;
}
.entry-date {
    display: flex;
    box-sizing: border-box;
    align-items: center;
    width: 180px;
}

.entry-date-number {
    font-size: 45px;
    font-weight: 700;
    color: #63CCCA;
    padding-right: 5px;
}

.entry-date-block {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    position: relative;
    top: 3px;
    left: 5px;
}

.entry-date-day {
    color: darkslategrey;
    font-weight: 300;
}

.entry-date-month {
    font-weight: 600;
    text-align: left;
}
.card-list {
    display: flex;
    flex-direction: column;
}
.searchbar {
    height: 75px;
    display: flex;
    align-items: center;
    width: 400px;
    position: relative;
    right: 20px;
    padding: 10px 0;
}

.searchbar form {
    width: 400px;
}


.searchbar form input {
    font-size: 25px;
    border: none;
    border-bottom: 1px solid black;
    width: 350px;
}

.searchbar form input:focus {
    outline: none;
    text-shadow: 2px 4px 3px rgba(0,0,0,0.3);
}

.searchbar form input:focus::-webkit-input-placeholder{
    color:transparent;
    text-shadow: none;
}

.searchbar form input:focus::-moz-placeholder{
    color:transparent;
    text-shadow: none;
}

.searchbar form input:focus:-ms-input-placeholder{
    color:transparent;
    text-shadow: none;
}

.searchbar form input:focus::-ms-input-placeholder{
    color:transparent;
    text-shadow: none;
}

.searchbar form input:focus::placeholder{
    color:transparent;
    text-shadow: none;
}

.searchbar form button {
    font-size: 25px;
    border: none;
}

.searchbar form button:focus {
    outline: none;
}
.entry-page {
    /* display: flex; */
    width: 80%;
    text-align: left;
    height: 100%;
    margin: 0 auto;
}

.entry-page-bookmark {
    position: absolute;
    top: -5px;
    right: 50px;
    font-size: 50px;
}

.entry-page-bookmark svg {
    color: black;
    -webkit-transition: .3s ease-out;
    transition: .3s ease-out;
}

.entry-page-bookmark svg:hover {
    color: rgb(180, 180, 180);
}

.entry-back-arrow {
    font-size: 45px;
    margin-right: 20px;
    position: relative;
    top: 50px;
    left: -75px;
    -webkit-transition: .3s;
    transition: .3s;
}

.entry-back-arrow:active {
    /* box-shadow: 0 5px #666; */
    -webkit-transform: translateX(-6px);
            transform: translateX(-6px);
}

.entry-body {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.entry-header {
    display: flex;
    justify-content: flex-start;
}

.entry-title {
    border: none;
    font-size: 32px;
    outline: none;
    padding-left: 25px;
    width: 100%;
    font-weight: 500;
}

.entry-text {
    text-align: left;
    white-space: pre-wrap;
    width: 100%;
    padding: 20px 0;
    font-size: 28px;
    font-weight: 300;
    display: block;
}


.entry-text:focus {
    outline: none
}

.entry-text-stamp {
    color: antiquewhite;
    background: black !important;
    border-radius: 8px;
    font-size: 1em;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; 
}

