.stamp-hidden {
    background: lightskyblue;
    width: 220px;
    height: 280px;
    border-radius: 0 10px 10px;
    visibility: hidden;
    opacity: 0;
    -webkit-transition: visibility 0.15s, opacity 0.2s linear;
    transition: visibility 0.15s, opacity 0.2s linear;
    position: absolute;
}

.text-stamp-container {
    display: inline-block;
    margin: 0 10px;
    text-decoration: none;
}
.entry-bar {
    /* width: 100%; */
    display: flex;
    position: absolute;
    bottom: -8px;
    border: 1.4px solid rgb(145, 145, 145);
    background: rgb(212, 212, 212);
    border-radius: 5px;
    border-bottom: none;
    height: 70px;
    width: 70%;
    align-items: center;
    justify-content: space-between;
}

.entry-bar h4 {
    margin: 5px;
    padding: 0;
}

.entry-bar-split::after {
    border: .4px solid rgb(100, 100, 100);
    content: '';
    font-size: 37px;
}

.entry-bar-section {
    width: 100%;
    padding: 0 10px 0;
    display: flex;
    flex-direction: column;
    height: 100%;
}

.entry-tiles {
    display: flex;
}
.entry-stamp {
    border: 1px solid skyblue;
    padding: 5px;
    border-radius: 25px;
    background: skyblue;
    margin: 0.5px 2px;
    font-weight: 100;
    -webkit-transition: .15s;
    transition: .15s;
    -webkit-user-select: none; /* Chrome/Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+ */
    color: rgb(29, 29, 29);
}

.entry-stamp-active {
    opacity: 80%;
}

.entry-stamp:hover {
    box-shadow: 0px 3px 10px rgba(0,0,0,0.1);
}

.entry-stamp:active {
    -webkit-transform: translateY(2px);
            transform: translateY(2px);
    box-shadow: 0px 3px 10px rgba(0,0,0,0.2);
}

.block-weekly {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.weekly-board {
    display: flex;
}

.weekly-day {
    box-sizing: border-box;
    background-color: white;
    border: 1px solid rgb(144, 144, 144);
    border-left: none;
    width: 25px;
    height: 25px;
    -webkit-transition: .4s ease;
    transition: .4s ease;
    display: flex;
    justify-content: center;
    align-items: center;
}

.weekly-day:hover {
    background-color: rgb(193, 231, 246)
}

.weekly-day-left {
    border-left: 1px solid rgb(144, 144, 144);
    border-radius: 3px 0px 0px 3px;
}

.weekly-day-right {
    border-radius: 0px 3px 3px 0px;
}

.day-active {
    background-color: rgb(187, 197, 251);
}

.meridiem {
    -webkit-transition: 1s;
    transition: 1s;
}

.clock-block {
    display: flex;
    justify-content: center;
}

.clock-block input {
    width: 15px;
}

.block-clock {
    justify-content: center;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}

.clock-block input:focus {
    outline: none;
}
.block-count {
    display: flex;
    width: 100%;
    align-items: center;
    font-size: 35px;
    justify-content: center;
}

.block-count h2 {
    display: none;
}

.block-count input {
    width: 85px;
    border: none;
    background: inherit;
    outline: none;
    font-size: 35px;
}

.units {
    width: 105px !important;
    min-width: 85px;
    padding-left: 10px;
}

.number {
    max-width: 100px !important;
    width: 50px !important;
}


