    font-size: 35px;
    background: inherit;
}

.block-title:focus {
    outline: none;
}
.yes-no {
    display: flex;
    outline: 1px solid black;
    margin: 0;
    border-radius: 25px;
}

.block-bool {
    -webkit-transition: .4s ease-in-out;
    transition: .4s ease-in-out;
    padding: 5px 7px;
    background: white;
}

.bool-active {
    background: black;
    color: white;
}
.log-view {
    width: 60%;
    margin: 50px auto;
    display: flex;
}

.log-view button {
    height: 50px;
    width: 200px;
