    margin: 5px 0;
    background-color: aqua;
}

.log-block .block-weekly {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 65%;
    background-color: aliceblue;
    margin: 0 auto;
    border-radius: 15px;
}

.log-block .block-notepad {
    margin: 0 auto;
}

.log-block .yes-no {
