}

.icon-left:active {
    -webkit-transform: translateX(-3px);
            transform: translateX(-3px);
}

.icon-right {
    position: relative;
    left: -60px;
    -webkit-transition: .8s ease-in-out;
    transition: .8s ease-in-out;
}

.icon-right:active {
    -webkit-transform: translateX(3px);
            transform: translateX(3px);
}
